.nft-jacker {
    .player-container.show {
        display: flex;
        .remix-player {
            margin: auto;
            position: relative;
            z-index: 100000;

            .close-button {
                position: absolute;
                font-size: 16pt;
                right: 25px;
                top: 0px;
                cursor: pointer;
            }

            .audio-player.disabled {
                pointer-events: none;
            }
            
            .audio-player {
                .progress-container {
                    display: flex;
                    width: 100%;
                    margin-bottom: 15px;
                    .progress-bar {
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
                display: flex;
                margin-left: -30px;
                background-color: black;
                padding: 30px;
                border-radius: 30px;
                box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
                iframe.spore-heatmap {
                    margin: unset;
                    position: unset;
                    border: 4px solid white !important;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .nft-jacker {
        .player-container.show {
            .remix-player {
                .audio-player {
                    margin-left: 0px;
                    padding: 20px;
                }
            }
        }
    }
}
